import React from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'

import pic11 from '../assets/images/pic11.jpg'

const LoadingSpeeds = (props) => (
    <div>
        <Helmet>
            <title>Loading Speeds</title>
            <meta name="description" content="Multi-device Support" />
        </Helmet>

        <div id="main" className="alt">
            <section id="one">
                <div className="inner">
                    <header className="major">
                        <h1>Fast Loading Speeds</h1>
                    </header>
                    <span className="image main"><img src={pic11} alt="" /></span>
                    <p>Today, people have high expectations for mobile experiences. Simply loading on a mobile device is no longer enough. To keep people engaged, mobile sites must be fast and relevant.</p>
                    <p>Unfortunately, based on our analysis of 10,000+ mobile web domains, we found that most mobile sites don’t meet this bar: The average load time for mobile sites is 19 seconds over 3G connections.1 That’s about as long as it takes to sing the entire alphabet song!</p>
                    <p>Slow loading sites frustrate users and negatively impact publishers. In our new study, “The Need for Mobile Speed”, we found that 53% of mobile site visits are abandoned if pages take longer than 3 seconds to load.</p>
                    <p>Additionally, sites that load in 5 seconds vs 19 seconds observed:
                      <ul className="inner">
                        <li>25% higher ad viewability</li>
                        <li>70% longer average sessions</li>
                        <li>35% lower bounce rates</li>
                      </ul>
                    </p>
                    <p>The study also explored the relationship between page speed and revenue. While there are several factors that impact revenue, our model projects that publishers whose mobile sites load in 5 seconds earn up to 2x more mobile ad revenue than those whose sites load in 19 seconds.</p>
                    <ul className="pagination">
                        <li><Link to="multi_device"className="button small">Prev</Link></li>
                        <li><Link to="performance" className= "page">1</Link></li>
                        <li><Link to="multi_device" className="page">2</Link></li>
                        <li><Link to="#" className="page active">3</Link></li>
                        <li><Link to="base_technologies" className="page">4</Link></li>
                        <li><Link to="multi_device" className="page">5</Link></li>
                        <li><Link to="multi_device" className="page">6</Link></li>
                        <li><Link to="base_technologies" className="button small">Next</Link></li>
                    </ul>
                </div>
            </section>
        </div>

    </div>
)

export default LoadingSpeeds


